<template>
  <div class="notice wrapper">
      <div class="container clearfix">
        <div class="pull-left notice-icon">
          <ul id="notice" :style="{top: pTop + 'px'}">
            <li v-for="item in newsList" :key="item.id">
              <!-- <a href="https://help.crypto.hkcexp.com/zh-cn/notice"></a> -->
              <a v-if="$i18n.locale === 'zn'" href="https://help.crypto.hkcexp.com/zh-cn/notice">{{ item.title }}</a>
              <a v-if="$i18n.locale === 'zh-hk'" href="https://help.crypto.hkcexp.com/zh-tw/notice">{{ item.title }}</a>
              <a v-if="$i18n.locale === 'en'" href="https://help.crypto.hkcexp.com/en-us/notice">{{ item.title }}</a>
            </li>
          </ul>
        </div>
        <div class="pull-right notice-more">
          <a v-if="$i18n.locale === 'zn'" href="https://help.crypto.hkcexp.com/zh-cn/notice">MORE :</a>
          <a v-if="$i18n.locale === 'zh-hk'" href="https://help.crypto.hkcexp.com/zh-tw/notice">MORE :</a>
          <a v-if="$i18n.locale === 'en'" href="https://help.crypto.hkcexp.com/en-us/notice">MORE :</a>
        </div>
      </div>
    </div>
</template>

<script>
  import { getArticleList } from '@/api/index'
  export default {
    data() {
      return {
        pTop: 0,
        newsList: []
      }
    },
    computed: {
      height: function() {
        return this.newsList.length * 50
      }
    },
    mounted() {
      this.setHeight()
      this.initNews()
    },
    methods: {
      setHeight() {
        var pTop = 0
        const _this = this
        window.setInterval(function() {
          pTop = pTop - 50
          if (Math.abs(pTop) >= _this.height) {
            pTop = 0
          }
          _this.pTop = pTop
        }, 4000)
      },
      initNews() {
        const data = {
          page: 1,
          per_page_num: 5
        }
        getArticleList(data).then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.newsList = [...res.data.list]
        }).catch(() => {
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .notice{
    background: #f6f9fc;
    box-shadow: 0 5px 5px #f3f3f9;
    z-index: 3;
    line-height: 50px;
    font-size: 12px;
    cursor: pointer;
    a{
      color: #1fa8f6;
    }
  }
  .notice .notice-icon{
    background: url(/static/images/index/notice.png) no-repeat left;
    padding-left: 30px;
    margin-bottom: 0;
    position: relative;
    overflow: hidden;
    width: 1000px;
    height: 50px;
  }
  .notice .notice-icon ul{
    position: absolute;
    top: 0;
    width: 100%;
    transition: all 0.8s;
  }
  .notice .notice-icon ul li{
    height: 50px;
  }
  .notice-more a{
    color: #909399;
    font-size: 12px;
  }
  .notice-more a:hover{
    text-decoration: underline;
  }
</style>



